@-moz-keyframes menuFadein {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0px);
  }
}

@-webkit-keyframes menuFadein {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0px);
  }
}

@keyframes menuFadein {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0px);
  }
}

.menu {
  .menu-item {
    margin-top: 24px;
    cursor: pointer;
    white-space: nowrap;

    a {
      color: #93A1B8;
      text-decoration: none;
      display: flex;

      img {
        margin-left: 8px;
      }
    }

    .text {
      display: flex;
      border: 0;
    }

  }

  .eoc-item {
    .tooltip {
      background-color: transparent;
    }
  }

  .deri-fi-box {
    width: 240px;
    height: 160px;
    background-image: url("../../assets/img/deri-fi.png");
    // background-position: 0 -40px;
    background-repeat: no-repeat;
    border-radius: 10px;
    border: 1px solid #FF780E;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: relative;

    .bg-color-box {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      width: 240px;
      height: 160px;
      background: linear-gradient(144.03deg, #FFFFFF -25.43%, rgba(255, 255, 255, 0) 56.81%);
      border-radius: 10px;
    }

    .deri-fi-text {
      width: 240px;
      height: 63px;
      background: #0A172B;
      border-radius: 0px 0px 10px 10px;
      padding: 8px 16px 15px 16px;
      font-size: 14px;
      font-weight: 500;

      .deri-fi-logo-title {
        display: flex;
        font-weight: 700;
        margin-bottom: 4px;

        img {
          margin-right: 8px;
          margin-left: 0;
        }
      }
    }

  }

  .deri-lite-box {
    background-image: url("../../assets/img/deri-lite.svg");
    border: 1px solid #172E4D;
    background-position: 0 0px;

    .bg-color-box {
      background: linear-gradient(144.03deg, rgba(210, 210, 210, 0.6) -25.43%, rgba(208, 208, 208, 0) 56.81%);
    }

    .deri-fi-text {
      background: linear-gradient(0deg, #0A172B 49.21%, rgba(10, 23, 43, 0) 115.87%);
      border-radius: 0px 0px 10px 10px;
    }
  }

  .trade-to-earn-item,
  .docs-item,
  .more-item {
    display: flex;
    align-items: center;

    .sub-item {
      display: flex;
      align-items: center;
    }
  }

  .docs-item,
  .more-item {
    img {
      margin-top: 5px;
    }
  }

  .trade-to-earn-item,
  .practice-item {
    display: flex;
    width: 130px !important;
    justify-content: space-between;

    a {
      margin-left: 5px;
    }
  }

  .menu-item.selected a {
    color: #E0ECFF;
    cursor: pointer;
    background: #5577FD1A;
  }

  .sub-menu-item {
    padding: 16px;
    text-align: left;
    border: 1px solid transparent;
    height: 48px;
  }

  .sub-menu-item:hover {
    // border: 1px solid #CD7A37;
    border-radius: 4px;
    background: #5577FD1A;
  }

  .sub-menu-item:last-child {
    margin-bottom: 24px;
  }

  .__react_component_tooltip.show {
    opacity: 1;
    width: 216px;
    padding: 0;
    position: absolute;
  }

  .__react_component_tooltip.show::after {
    content: none;
  }

  .__react_component_tooltip.show.place-right {
    margin: 0px;
  }

  .footer {
    display: none;
  }
}

.powers-item {
  display: flex;

  a {
    margin-left: 5px;
  }
}


@media only screen and (max-width : 375px) {

  .show-side-menu {
    .menu {
      opacity: 1;
      width: 100%;
      display: flex;
      z-index: 2;
      animation: menuFadein .3s .0s linear;
      transform: translateX(0px);
    }

    .footer {
      display: block;
      bottom: 20px;
      position: fixed;
    }
  }
}

@media only screen and (max-width : 760px) {
  .menu {
    background: #0A172B;
    position: fixed;
    flex-direction: column;
    top: 0;
    left: 0;
    padding: 24px 32px;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: flex;
    flex-direction: column;
    background: #0A172B;

    .sub-menu-item {
      height: 30px;
    }

    .menu-item {
      .beta-icon {
        width: 42px;
      }
    }

    .back-icon {
      display: block;
    }

    .footer {
      display: none;
    }
  }

  .show-side-menu {
    .menu {
      opacity: 1;
      width: 100%;
      display: flex;
      z-index: 2;
      animation: menuFadein .3s .0s linear;
      transform: translateX(0px);
    }

    .footer {
      display: block;
      bottom: 20px;
      position: fixed;
    }
  }
}

@media only screen and (max-width : 1361px) {
  .menu {
    background: #0A172B;
    position: fixed;
    flex-direction: column;
    top: 0;
    left: 0;
    padding: 24px 32px;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: flex;
    flex-direction: column;
    background: #0A172B;
    .anniversary-header-menu{
      margin-top: 24px;
    }
    .menu-item {
      .beta-icon {
        width: 42px;
      }
    }

    .back-icon {
      display: block;
    }

    .footer {
      display: none;
    }
  }

  .show-side-menu {
    .menu {
      opacity: 1;
      width: 100%;
      display: flex;
      z-index: 100;
      animation: menuFadein .3s .0s linear;
      transform: translateX(0px);
    }

    .footer {
      display: block;
      bottom: 20px;
      position: fixed;
    }
  }

}

@media only screen and (min-width : 1361px) {
  .menu {
    display: flex;
    justify-content: space-around;
    margin-left: 14px;
    font-size: 16px;
    background: none;
    position: initial;
    flex-direction: row;
    padding: 0px;

    .back-icon {
      display: none;
    }

    .menu-item.long {
      width: 128px
    }

    .menu-item {
      // position: relative;
      width: 96px;
      margin: auto;
      display: flex;
      justify-content: center;

      .beta-icon {
        position: absolute;
        top: -14px;
        left: 28px;
      }
    }

    .menu-item:hover>a,
    .menu-item:hover>.sub-menu,
    .menu-item.selected>a,
    .menu-item:hover,
    .sub-menu-item:hover>a {
      color: #E0ECFF;
      cursor: pointer;
    }

    .eoc-item:hover>a {
      color: #E0ECFF;
    }

    .__react_component_tooltip.show.place-bottom {
      margin-top: 26px;
      margin-left: 30px;
    }

    .footer {
      display: none;
    }
  }
}


