.risk-warning {
  width: 551px;
  height: 294px;
  background: #15213E;
  border: 1px solid #364F70;
  border-radius: 4px;
  color: #FFFFFF;
  font-weight: 400;

  .risk-header {
    height: 61px;
    border-bottom: 1px solid #203B60;
    display: flex;
    justify-content: space-between;
    padding: 0 35px;
    align-items: center;
    font-size: 18px;

    .risk-img {
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        margin-right: 8px;
      }

    }

    .close {
      cursor: pointer;
    }
  }

  .risk-box {
    padding: 0 35px;
    margin-top: 40px;
    font-size: 16px;
    color: #E0ECFF;
    height: 98px;
  }

  .risk-close {
    margin-top: 36px;
    display: flex;
    justify-content: end;

    .risk-close-btn {
      width: 160px;
      height: 40px;
      background: #5577FD;
      border-radius: 1000px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 35px;
      cursor: pointer;
      color: #E0ECFF;
    }
    .risk-close-btn:hover{
      color: #FFF;
    }
  }
}

@media only screen and (max-width: 759px) {
  .risk-warning {
    width: 375px;

    .risk-header {
      padding: 0 24px;
    }

    .risk-box {
      padding: 0 24px;

    }

    .risk-close {
      .risk-close-btn {
        margin-right: 24px;
      }
    }
  }
}