 .welcome-arbitrum {
   width: 100%;
   height: 100%;
   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   background: rgba(0, 0, 0, 0.4);
   display: flex;
   justify-content: center;
   align-items: center;
   z-index: 50;
   // }


   .welcome-box {
     width: 404px;
     height: 704px;
     border-radius: 8px;
     z-index: 99;
     background-image: url('./img/welcome.png');
     background-size: 100% 100%;
     padding: 34px 44px;
     background-repeat: no-repeat;
     font-size: 16px;
     font-weight: normal;

     .welcome-logo {
       display: flex;

       .deri-logo,
       .arbitrum-logo {
         width: 50px;
         height: 50px;
         display: flex;
         background: rgba(53, 38, 38, 0.1);
         justify-content: center;
         align-items: center;
         border-radius: 10px;
       }

       .deri-logo {
         margin-right: 18px;
       }
     }

     .welcome-title {
       margin-top: 24px;
       font-size: 41px;
       font-weight: 700;
       margin-bottom: 20px;
       align-items: center;
       display: flex;

       div {
         // text-shadow: 0px 8px 11px rgba(17, 33, 65, 0.79);
         background: linear-gradient(180deg, #FFFFFF 17.72%, rgba(255, 255, 255, 0) 100%);
         -webkit-background-clip: text;
         -webkit-text-fill-color: transparent;
         background-clip: text;
         text-fill-color: transparent;
       }

       .title-two {
         font-size: 40px;
         margin-top: 0px;
       }

     }

     .welcome-info {
       margin-top: 39px;

       .welcome-info-title {
         color: #FFFFFF;
         font-weight: 600;
       }

       ul {
         padding-left: 24px;
         margin-top: 8px;
         font-size: 20px;
         color: #E0ECFF;

         li {
           margin-bottom: 4px;
         }
       }
     }

     .learn-more-btn {
       display: flex;
       justify-content: space-between;
       align-items: center;
       color: #fff;
       font-size: 14px;
       margin-top: 72px;

       a {
         width: 184px;
         height: 40px;
         font-size: 20px;
         background: #3756CD;
         border-radius: 4px;
         display: flex;
         align-items: center;
         justify-content: center;
         cursor: pointer;
       }

       .btn-ok {
         width: 96px;
         height: 40px;
         border: 1px solid #E0ECFF;
         border-radius: 4px;
         display: flex;
         justify-content: center;
         align-items: center;
         color: #E0ECFF;
         cursor: pointer;
       }
     }

     .welcome-header-close {
       display: flex;
       align-items: center;
       justify-content: space-between;
       font-weight: 400;
       font-size: 20px;
       color: #FFF;
       img{
        cursor: pointer;
       }
     }

     .total-rewards-num {
       font-size: 32px;
       font-weight: 600;
       color: #FFF;

       .color-text {
         background: linear-gradient(180deg, #5A82F6 0%, rgba(255, 255, 255, 0) 100%), linear-gradient(91.34deg, #2698EA 11.48%, #FFB176 53.97%, #ED5158 100.98%);
         -webkit-background-clip: text;
         -webkit-text-fill-color: transparent;
         background-clip: text;
         text-transform: uppercase;
         font-size: 80px;
       }
     }

     .join-the-party {
       margin-top: 34px;

       a {
         display: flex;
         align-items: center;
         justify-content: center;
         width: 202px;
         height: 48px;
         background: #5577FD;
         border-radius: 133.984px;
         font-weight: 700;
         font-size: 20px;
       }
     }
   }

   @media only screen and (max-width:759px) {
     .welcome-box {
       width: 375px;
       height: 704px;
       padding: 24px 32px;

       .learn-more-btn {
         margin-top: 48px;
       }
     }
   }
 }