.intro-box {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 50;
}

.intro {
  width: 480px;
  height: 480px;
  position: fixed;
  z-index: 99;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #2C3B5E;
  background-image: url('./img/intro-bg.svg');
  background-size: 100%;
  background-repeat: no-repeat;
  overflow: hidden;

  .intro-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 40px 0 40px;

    .close-logo {
      cursor: pointer;
    }
  }

  .step {
    text-align: center;
    margin-top: 60px;
    color: #E0ECFF;
    width: 1440px;
    display: flex;

    .step-box {
      width: 480px;
      padding: 0 40px;
    }

    .step-title {
      margin-top: 24px;
      font-size: 24px;
      font-weight: 600;
    }

    .step-text {
      font-size: 18px;
      font-weight: normal;
      margin-top: 30px;
      text-align: left;
    }

  }

  .one {
    animation: switch 0.75s ease-in-out 0s 1 alternate forwards;
  }

  .two {
    animation: switchOne 0.75s ease-in-out 0s 1 alternate forwards;
  }

  .three {
    animation: switchTwo 0.75s ease-in-out 0s 1 alternate forwards;
  }

  @keyframes switch {

    0%,
    25% {
      margin-left: 0;
    }

    70%,
    100% {
      margin-left: 0;
    }

  }

  @keyframes switchOne {

    0%,
    25% {
      margin-left: 0;
    }

    70%,
    100% {
      margin-left: -480px;
    }

  }

  @keyframes switchTwo {

    0%,
    25% {
      margin-left: -480px;
    }

    70%,
    100% {
      margin-left: -960px;
    }

  }

  .step-check {
    width: 112px;
    height: 4px;
    margin: 0 auto;
    margin-top: 30px;
    display: flex;
    justify-content: space-between;

    .step-no {
      width: 32px;
      height: 4px;
      background-color: #C4C4C4;
      border-radius: 4px;
      cursor: pointer;
    }

    .now {
      background-color: #E0ECFF;
    }

  }

  .learn-next {
    width: 480px;
    height: 66px;
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 0 16px;
    background-color: #F1F1EF;
    display: flex;
    align-items: center;
    justify-content: space-between;

    a {
      color: #211814;
      font-size: 14px;
      text-decoration: underline;
    }

    .next-button {
      width: 96px;
      height: 35px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #59AE99;
      border-radius: 4px;
      border: 2px solid #59AE99;
      font-size: 16px;
      font-weight: bold;
      cursor: pointer;
    }
  }
}

@media only screen and (max-width : 759px) {
  .intro {
    width: 342px;
    height: 520px;

    .step {
      width: 1026px;

      .step-box {
        width: 342px;
      }
    }

    .two {
      animation: switchOne 0.75s ease-in-out 0s 1 alternate forwards;
    }

    .three {
      animation: switchTwo 0.75s ease-in-out 0s 1 alternate forwards;
    }

    @keyframes switchOne {

      0%,
      25% {
        margin-left: 0;
      }

      70%,
      100% {
        margin-left: -342px;
      }

    }

    @keyframes switchTwo {

      0%,
      25% {
        margin-left: -342px;
      }

      70%,
      100% {
        margin-left: -684px;
      }

    }

    .learn-next {
      width: 342px;
    }
  }
}

.open-show {
  width: 38px;
  height: 38px;
  position: fixed;
  background: #192B56;
  border-radius: 50%;
  bottom: 13px;
  right: 22px;
  display: flex;
  z-index: 10;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;

  .show-text {
    display: none;
  }
}

.open-show:hover {
  border-radius: 130px;
  width: 190px;
  .show-text {
    display: block;
    font-weight: 600;
    font-size: 18px;
    color: #E0ECFF;
    margin-left: 16px;
  }
}

