@import '../../assets/css/variable.less';

* {
  box-sizing: border-box;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 64px;

  .menu {
    display: flex;
    transform: inherit;
  }

  .left {
    display: flex;
    align-items: center;

    .logo {
      margin-left: 48px;
      display: flex;
      align-items: center;

      >a {
        color: @whiteFontColor;
        text-decoration: none;
        display: flex;
      }

      .name {
        margin: auto 12px;
        color: @whiteFontColor;
        font-size: @20FontSize;
      }
    }

    .deri-lite-pro-tab {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 154px;
      height: 32px;
      margin-left: 14px;
      color: #E0ECFF;
      background: #1A2D4A;
      border: 1px solid rgba(224, 236, 255, 0.3);
      border-radius: 16px;

      .deri-pro {
        display: flex;
        align-items: center;
        background-color: #5577FD;
        height: 32px;
        border-radius: 16px;
        flex-grow: 1;

        .deri-pro-text {
          flex-grow: 1;
          text-align: center;
          height: 32px;
          line-height: 32px;
        }
      }

      .m-tab {
        display: none;
      }

      .deri-lite {
        width: 60px;
        border-radius: 16px;
        line-height: 32px;
        height: 32px;
        text-align: center;
        cursor: pointer;
      }

      .tab-box {
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        width: 32px;
        height: 32px;
        background: #E0ECFF;
        box-shadow: -3px 0px 5px rgba(27, 40, 60, 0.43);
        border-radius: 50%;
      }

    }

    .anniversary-header-menu {
      width: 248px;
      height: 28px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      color: #FFF;
      border-radius: 5px;
      margin-left: 24px;
      background: linear-gradient(180deg, #5A82F6 0%, rgba(255, 255, 255, 0) 100%),
      linear-gradient(91.34deg, #2698EA 11.48%, #FFE6D3 53.97%, #ED5158 100.98%);

      div {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 12px;
        width: 246px;
        height: 26px;
        background-image: url('../../assets/img/header-anniversary-bg.png');
        border-radius: 5px;
        background-color: #1A2D4A;
        background-size: 100% 100%;
      }

      .color-text {
        background: linear-gradient(180deg, #5A82F6 0%, rgba(255, 255, 255, 0) 100%), linear-gradient(91.34deg, #2698EA 11.48%, #FFB176 53.97%, #ED5158 100.98%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-transform: uppercase;
        font-size: 20px;
      }
    }
  }

  .right {
    display: flex;
    align-items: center;
    margin-right: 24px;

    .work-href {
      margin-right: 16px;
    }
  }

  .small-menu {
    margin-left: 24px;
  }

}

@media only screen and (min-width: 0px) {
  .header {
    .right {
      margin-right: 0px;
    }
  }
}

@media only screen and (min-width: 1140px) {
  .header {
    .right {
      margin-right: 24px;
    }
  }
}

