@-moz-keyframes menuFadeout {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-100%);
  }
}

@-webkit-keyframes menuFadeout {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-100%);
  }
}

@keyframes menuFadeout {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-100%);
  }
}

//小于760 大于375px
@media only screen and (max-width : 375px) {
  .header {
    .left .logo {
      margin-left: 32px;
    }

    .m-connect {
      width: 130px !important;
    }

    .menu {
      display: flex;
      transform: translateX(-100%);
      -moz-animation: menuFadeout 0.2s 0s linear;
      -webkit-animation: menuFadeout 0.2s 0s linear;
      animation: menuFadeout 0.2s 0s linear;
    }

    .small-menu {
      display: block;
    }

    // .lang-select,
    .network-select {
      display: none;
    }
  }
}

@media only screen and (max-width : 760px) {

  .header {
    .left .logo {
      margin-left: 32px;
    }

    .name {
      display: none;
    }

    .left {
      .deri-lite-pro-tab {
        width: 62px;
        margin-left: 0;
        margin-left: 4px;

        .deri-pro {
          display: none;
        }

        .deri-lite {
          display: flex;
          align-items: center;
          width: 62px;
          height: 32px;
          border-radius: 16px;
          padding-left: 8px;
          background-color: #5577FD;

        }

        .m-tab {
          display: block;
          margin-right: 4px;
        }

      }
    }

    .m-connect {
      width: 130px !important;
    }

    .menu {
      display: flex;
      transform: translateX(-100%);
      -moz-animation: menuFadeout 0.2s 0s linear;
      -webkit-animation: menuFadeout 0.2s 0s linear;
      animation: menuFadeout 0.2s 0s linear;
    }

    .small-menu {
      display: block;
    }

    // .lang-select,
    .network-select {
      display: none;
    }

    .wallet-view {
      margin-right: 8px;

      .acc-wrapper {
        img {
          margin-right: 0;
        }

        .address {
          display: none;
        }
      }
    }
  }
}

@media only screen and (max-width : 1361px) {
  .header {
    .left .logo {
      margin-left: 24px;
    }

    .menu {
      display: flex;
      transform: translateX(-100%);
      -moz-animation: menuFadeout 0.2s 0s linear;
      -webkit-animation: menuFadeout 0.2s 0s linear;
      animation: menuFadeout 0.2s 0s linear;
    }

    .small-menu {
      display: block;
    }

    // .lang-select,
    // .network-select {
    //   display: none;
    // }

    .lang-select {
      margin-right: 8px;
    }
  }

}

@media only screen and (min-width : 1361px) {
  .header {
    .menu {
      display: flex;
      transform: inherit;
    }

    .small-menu {
      display: none;
    }

    .lang-select,
    .network-select {
      display: flex;
    }
  }

}


