@import './assets/css/variable.less';

html[lang=zh-TW],
html[lang=zh-CN] {
    body {
        font-weight: normal;
    }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0;
    /* <-- Apparently some margin are still there even though it's hidden */
}

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

/* Track */
::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #203B60;
    border-radius: 10px;
}

input[type=number] {
    -moz-appearance: textfield;
    /* Firefox */
}

body {
    background-color: @background_color;
    font-family: Barlow;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    color: @defaultFontColor;
}


@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

@-webkit-keyframes fadeOut {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

@-webkit-keyframes pulse {
    50% {
        opacity: .5
    }
}

@keyframes pulse {
    50% {
        opacity: .5
    }
}

.loading-line {
    width: 100px;
    height: 16px;
    border-radius: 4px;
    background-color: #203B60;
    animation: pulse 2s cubic-bezier(0.2, 0, 0.4, 1) infinite;
    display: inline-block;
}

.loading.hide {
    display: none;
}

// .loading {
//     width: 100px;
//     position: absolute;
//     top: 50%;
//     left: 50%;
// }
.loading {
    width: 30px;
    height: 30px;
    border: 2px solid #E0ECFF;
    border-top-color: transparent;
    border-radius: 100%;
    animation: circle infinite 0.75s linear;
    transform: translate(-50%, -50%);
    width: 50px;
    height: 50px;
    transform: translate(-50%, -50%);
    position: absolute;
    top: 50%;
    left: 50%;
}

@keyframes circle {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(360deg);
    }
}

.message-pool-bsc {
    width: 100%;
    padding: 12px 24px;
    background: #222021;
    font-size: 16px;
    color: #FFB110;
    min-width: 375px;
    display: flex;
    justify-content: center;
    align-items: center;
    a{
        text-decoration: underline;
        color: #FFB110;
        margin-left: 4px;
    }
    img{
        width: 12px;
        height: 12px;
        margin-left: 24px;
        cursor: pointer;
    }
}