#__react-alert__ {
  div>div {
    pointer-events: auto !important;
  }

  div {
    z-index: 999999 !important;
  }
}

.transaction-state {
  width: 336px;
  background: #1A2D4A;
  border-radius: 4px;
  font-size: 16px;
  color: #E0ECFF;
  position: fixed;
  bottom: 32px;
  right: 16px;
  z-index: 9999999;

  .close {
    width: 22px;
    height: 22px;
    cursor: pointer;
    background: #1A2D4A;
    border: 1px solid #E0ECFF;
    border-radius: 50%;
    position: absolute;
    right: -6px;
    top: -9px;
    z-index: 9;

    .close-icon {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .message-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 16px;
    height: 56px;
    border-bottom: 1px solid #203B60;
    position: relative;

    .title-slider-box {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 1px;
      left: 0px;
      background-color: rgba(85, 119, 253, 0.2);

      .title-slider-value {
        height: 1px;
        background-color: #5577FD;
      }

      .trade-start-value {
        animation: half 10s;
        animation-fill-mode: forwards;
      }

      .trade-ending-value {
        animation: all 10s;
        animation-fill-mode: forwards;
      }

      .trade-half-value {
        width: 50%;
        // transition: 3s;
      }

      .trade-end-value {
        width: 100%;
        // transition: 3s;
        background-color: #59AE99;
      }

      .trade-error-value {
        width: 100%;
        background-color: #CE5E5E;
      }
    }

    .icon {
      display: flex;
      align-items: center;

      img {
        margin-right: 5px;
      }

    }

    .state-box {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-weight: 400;
      cursor: pointer;
      font-size: 14px;
      color: #E0ECFF;

      span {
        margin-right: 8px;
      }
    }
  }

  .message-text-link {
    padding: 0 24px;
    padding-bottom: 28px;
    padding-top: 28px;
    display: flex;
    align-items: center;
    min-height: 68px;


    .link {
      font-size: 16px;
    }

    .error {
      .message-no-hash {
        color: #CE5E5E;
        font-size: 16px;

        .is-show-error {
          text-decoration: underline;
          cursor: pointer;
        }
      }
      .socket-error {
        color: #CD7A37;
      }
    }


    .trade-info-box {
      width: 100%;
      height: 100%;

      .info-box {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 15px;
        color: #E0ECFF;
        font-size: 14px;
        margin-bottom: 4px;

        .info-box-title {
          color: #93A1B8;
        }

        .info-box-value {
          .loading {
            width: 14px;
            height: 14px;
            position: static;
            border: 1px solid #E0ECFF;
            border-top-color: transparent;
          }
        }
      }

      .info-box:first-child {
        margin-bottom: 24px;
      }
    }

  }
}

@keyframes half {
  0% {
    width: 0%;
  }

  100% {
    width: 50%;
  }
}

@keyframes end_half {
  100% {
    width: 50%;
  }
}

@keyframes all {
  0% {
    width: 50%;
  }

  100% {
    width: 95%;
  }
}

@keyframes end_all {
  100% {
    width: 100%;
  }
}